<template>
  <div>
    <div class="operating">
      <div class="left">
        <template v-if="!isMove">
          <span @click="isUpload=true"></span>
          <span @click="addDir"></span>
          <span class="hide-i-768" @click="removeResource(1)"></span>
          <span class="hide-i-768" @click="handleAllSelect">{{isAllSelect ? '' : ''}}</span>
        </template>
        <template v-else>
          <span @click="handleMove"></span>
          <span @click="isMove=false;moveId=null;"></span>
        </template>
      </div>
      <div class="right hide-i-768" v-if="!isMove">
        <div class="search">
          <input
            type="text"
            v-model="filterParams.keyword"
            @keyup.enter="handleSearch"
            placeholder=""
          >
        </div>
      </div>
    </div>
    <resource-path style="margin-top: 10px;" @change="getListData" :sources="pathList" :active="filterParams.parent_id"/>
    <div class="image-list">
      <resource-item
        v-for="item in resourceList"
        :data="item"
        :key="item.id"
        @next="getListData"
        @changeName="handleChangeName"
        @previewImage="handlePreviewImage"
        @onSelect="handleSelect"
      />
    </div>
    <resource-right-menu :sources="resourceList" @change="handleRightChange"/>
    <resource-preview-image :sources="previewImageList" v-if="isPreview"  @close="isPreview=false"/>
    <resource-upload
      action="https://resource.yjgeek.com/api/file/upload"
      :headers="headers"
      v-if="isUpload"
      @close="isUpload=false; getListData(filterParams.parent_id);"
      :onSuccess="addResource"
    />
    <input type="text" id="copy-url" placeholder="url" >
    <el-dialog
      title="README"
      :visible="dialogVisible"
      width="50%"
      :append-to-body="true"
      :before-close="handleClose">
      <div class="readme">
        <p>: <strong>NodejsKoaVue</strong></p>
        <ol>
          <li>(: )</li>
          <li></li>
          <li>(200*200)</li>
          <li>GIF</li>
          <li></li>
          <li>()</li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li>!!!</li>
        </ol>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="handleClose"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import mixin from './mixin'
import ResourceSelectDialog from './children/SelectDialog'
export default {
  name: "resource-list",
  mixins: [mixin],
  data () {
    return {
      dialogVisible: sessionStorage.dialogVisible ? false : true,
    }
  },
  components: {
    ResourceSelectDialog
  },
  methods: {
    handleClose () {
      this.dialogVisible = false;
      sessionStorage.dialogVisible = true;
    }
  }
};
</script>

<style lang="scss">
.operating {
  background: #fff;
  padding: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
  display: flex;
  justify-content: space-between;
  .left {
    display: flex;
    span {
      padding: 5px 0;
      border: 1px solid #000;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100px;
      cursor: pointer;
      & + span {
        margin-left: 10px;
      }
    }
  }
  .right {
    input {
      padding: 10px;
      border: 1px solid #efefef;
      &:focus {
        outline: none;
      }
    }
  }
}
.image-list {
  display: flex;
  margin-top: 15px;
  flex-wrap: wrap;
}
.readme li{
  
  line-height: 30px;
}
#copy-url {
  position: fixed;
  left: -100%;
  bottom: -100%;
}
@import "./response.scss";
</style>



